.App {
  text-align: center;
}

.Profile-pix {
  height: 40vmin;
  pointer-events: none;
}
img.Profile-pix {
  border-radius: 50%;
}

@media (prefers-reduced-motion: no-preference) {
  .Profile-pix {
    animation: Profile-pix-spin infinite 10s cubic-bezier(0.45, 0.25, 0.60, 0.95);;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes Profile-pix-spin {
  from {
    transform: rotate(-45deg);
  }
  to {
    transform: rotate(45deg);
  }
}

.social-media-link {
  width: 600px;
  display: grid;
  grid-template-columns: auto auto auto;
  justify-content: space-evenly;
}
.intro {
  font-size: .9em;
  font-weight: normal;
  padding: 20px;
}

footer {
  position: fixed;
  bottom: 0px;
  width: 100%;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',monospace;
  display: flex;
  justify-content: center;
  margin-bottom: auto;
}

@media(max-width: 480px){
  .social-media-link {
    display: grid;
    grid-template-rows: auto auto auto;
    grid-template-columns: unset;
    justify-content: unset;
    align-items: unset;
  }
}
